.player-wrapper {
  position: relative;
  padding-top: 56.25%;
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}

.title {
  position: fixed;
  z-index: 1;
  margin-top: -0.3rem;
  padding: 6px;
  display: inline-flex;
  width: 100%;
  background-color: #f6f3f1;
}

.learn {
  position: relative;
  width: 100%;
  height: 100vh;
  margin-top: 4.5rem;
  padding-top: 0.5%;
  background-color: #fcfcfc;
  font-family: Montserrat-Regular;
  display: inline-block;
}

.sidenav {
  background-color: white;
  position: fixed;
  z-index: 1;
  top: 10rem;
  left: 0px;
}

.sidenav-close {
  background-color: white;
  position: fixed;
  z-index: 1;
  top: 10rem;
  left: -14.5rem;
}

.menu {
  width: 16rem;
  height: 500px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-top: 8px;
  padding-bottom: 20px;
}

.main {
  margin-left: 16rem;
  margin-top: 4rem;
}

.main-closed {
  margin-left: 0;
  margin-top: 5rem;
}

.content-list {
  font-family: Montserrat-Bold;
}

.heading {
  margin-left: 5.5%;
}

@media only screen and (max-width: 1000px) {
  .main {
    margin-left: 16rem;
    margin-top: 5rem;
  }
  .main-closed {
    margin-left: 2rem;
    margin-top: 5rem;
  }
  .menu {
    width: 14rem;
  }
  .sidenav-close {
    left: -13rem;
  }
}

@media only screen and (max-height: 500px) {
  .menu {
    height: 300px;
  }
}

@media only screen and (max-height: 300px) {
  .menu {
    height: 100px;
  }
}

@media only screen and (max-width: 400px) {
  .main {
    margin-left: 16rem;
    margin-top: 5rem;
  }
  .main-closed {
    margin-left: 2rem;
    margin-top: 5rem;
  }
  .menu {
    width: 14rem;
  }
  .sidenav-close {
    left: -13rem;
    top: 12rem;
  }
}
