body {
  /* background: #E9E6E3; */
  font-family: Montserrat-Bold;
}
.row-header {
  margin: 0px auto;
  padding: 0px auto;
}

.row-content {
  margin: 0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height: 400px;
}

.footer {
  background-color: #d1c4e9;
  margin: 0px auto;
  padding: 20px 0px 20px 0px;
}

.navbar .active a {
  border-bottom: 3px solid #eb7a9e;
  padding-bottom: 0px;
}

.navbar {
  background: transparent !important;
  transition: background 0.7s;
  padding-top: 13px !important;
  padding-bottom: 13px !important;
  padding-left: 50px !important;
  padding-right: 50px !important;
}

.navbar li a {
  color: #000 !important;
}
.hom li a {
  color: #fff !important;
}
.hom .navbar-brand {
  color: #fff !important;
}
.hom .dropdown-toggle::after {
  border: solid #fff !important;
  border-width: 0 3px 3px 0 !important;
}
.dropdown-toggle {
  transition: none !important;
}
.scrol {
  background: #fff !important;
  color: unset !important;
  box-shadow: 0px 3px 5px rgba(216, 127, 127, 0.2);
}
.scrol .dropdown-toggle::after {
  border: solid #000 !important;
  border-width: 0 3px 3px 0 !important;
}
.scrol li a,
.scrol .navbar-brand {
  color: #000 !important;
}
.scrol .courses-btn {
  color: #fff !important;
}
.navbar-brand {
  color: #000;
}

.navbar-light .navbar-toggler {
  border: none !important;
  outline: none !important;
  margin-right: 1% !important;
}

.Montserrat-Light {
  font-family: Montserrat-Light;
}
.Montserrat-Bold {
  font-family: Montserrat-Bold;
  font-weight: bold;
}
.LemonMilk {
  font-family: Montserrat-Bold;
}
.cyan,
.cyan:hover {
  border-radius: 25px !important;
  /* z-index: 4; */
  justify-content: center;
  background: #06bfad !important;
  border: none !important;
  outline: none !important;
  color: #fff !important;
  font-family: Montserrat-Bold !important;
  box-shadow: 2px 2px 10px 0 #06bfad;
  height: 42px !important;
  width: 130px !important;
  font-size: 15px !important;
}
.btn-warning {
  border-radius: 25px !important;
  /* z-index: 4; */
  justify-content: center;
  background: #e03268 !important;
  border: none !important;
  outline: none !important;
  color: #fff !important;
  font-family: Montserrat-Bold !important;
  box-shadow: 2px 2px 10px 0 #e03268;
  height: 42px;
  width: 130px;
  font-size: 15px;
}
@media (min-device-width: 900px) {
  .nav-item {
    margin: 0.3rem;
  }
}
@media (max-device-width: 900px) {
  .navbar .active a {
    border-bottom: none !important;
  }
  .nav-item {
    margin: 0.5rem;
    border-bottom: 1px solid #e6e6e6 !important;
  }
  .nav-item:nth-last-child(2),
  .nav-item:nth-last-child(1) {
    border-bottom: none !important;
  }
  .navbar-collapse {
    min-height: 100vh !important;
    margin-top: 5%;
  }
  .navbar {
    padding-right: 13px !important;
    padding-left: 13px !important;
  }
}
@media (max-device-width: 450px) {
  body {
    scrollbar-width: 0px;
  }
}
.nav-link {
  font-family: Montserrat-Regular;
  /* font-family: Montserrat-Light; */
  font-weight: bold;
}
.dropdown-toggle {
  transition-duration: 2s;
  transition-delay: 2s;
}
.dropdown-toggle::after {
  display: inline-block;
  margin-left: 0.5em;
  vertical-align: 0.255em;
  border: solid black;
  border-width: 0 3px 3px 0;
  padding: 3px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.dropdown-menu {
  border: 0px;
  /* z-index: 6; */
  box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2);
}
.dropdown-item {
  font-family: Montserrat-Regular;
}
.dropdown-item:hover {
  background: #e6fefc !important;
  color: #212529 !important;
}

.before-select {
  background: #fff !important;
  border-color: #fff !important;
}
.round-btn {
  border-radius: 25px !important;
  vertical-align: middle;
  /* z-index: 3; */
  justify-content: center;
  background: #06bfad !important;
  border: 0px;
  color: #fff !important;
  font-family: Montserrat-Bold !important;
  box-shadow: 2px 2px 10px 0 #06bfad;
  height: 37px;
  width: 130px;
  text-transform: capitalize !important;
}
.courses-btn {
  border-radius: 25px !important;
  vertical-align: middle;
  /* z-index: 3; */
  justify-content: center;
  background: #06bfad !important;
  border: 0px;
  color: #fff !important;
  font-family: Montserrat-Bold !important;
  box-shadow: 2px 2px 10px 0 #06bfad;
  height: 37px;
  max-width: 110px;
  text-transform: capitalize !important;
}
.pinkbtn {
  border-radius: 25px !important;
  vertical-align: middle;
  /* z-index: 3; */
  justify-content: center;
  background: #e03268 !important;
  border: 0px;
  color: #fff !important;
  font-family: Montserrat-Bold !important;
  box-shadow: 2px 2px 10px 0 #e03268;
  height: 37px;
  max-width: 110px;
  text-transform: capitalize !important;
}
.btn-black {
  border-radius: 25px !important;
  vertical-align: middle;
  /* z-index: 3; */
  justify-content: center;
  background: #000 !important;
  border: 0px;
  color: #fff !important;
  font-family: Montserrat-Bold !important;
  box-shadow: 2px 2px 10px 0 #000;
  height: 40px;
  width: 120px;
  text-transform: capitalize !important;
}
.btn-white {
  border-radius: 25px !important;
  vertical-align: middle;
  /* z-index: 3; */
  justify-content: center;
  background: #fff !important;
  border: 0px;
  color: #000 !important;
  font-family: Montserrat-Bold !important;
  box-shadow: 2px 2px 10px 0 #fff;
  height: 40px;
  width: 90px;
  text-transform: capitalize !important;
}
.overlay {
  max-width: 100%;
  margin: 70px 0;
  padding: 60px 25px;
  background-color: whitesmoke;
  font-family: Arial;
  font-size: 16px;
  line-height: 1.5rem;
  letter-spacing: 1px;
}

.modal-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: inline-block;
  justify-content: center;
  padding: 50px 20px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 3;
}

.popup {
  border-radius: 8px;
  font-family: Montserrat-Regular;
  position: relative;
  margin-bottom: 100px;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  height: -moz-fit-content;
  width: 400px;
  background-color: whitesmoke;
}

@media only screen and (max-width: 600px) {
  .popup {
    width: 350px;
  }
}

.btnFacebook {
  margin-top: 15px;
  width: 100%;
  height: 50px;
  border-radius: 4px;
  background: #3b5998;
  color: white;
  border: 0px transparent;
  text-align: center;
  display: inline-block;
  font-size: small;
  outline: "none";
}

.btnGoogle {
  margin-top: 10px;
  margin-bottom: 15px;
  width: 100%;
  height: 50px;
  border-radius: 4px;
  background: #db3236;
  color: white;
  border: 0px transparent;
  text-align: center;
  outline: "none";
}

.error {
  font-size: small;
}

.password {
  border-radius: 0.25rem;
  background-color: #fff;
}

.border-bottom1 {
  border-bottom: 1px solid #dee2e6 !important;
}
.d-flex {
  display: flex !important;
}

.prevscol {
  overflow: hidden;
  transition: all 0.1ms !important;
}
.prevscol .navbar {
  background-color: #fff !important;
}
.prevscol .navbar .dropdown-toggle::after {
  color: #000;
  border-width: 0 3px 3px 0;
}
