.grid-display {
  display: grid;
  grid-template-columns: 1fr 1fr 0.9fr 0.6fr 1.5fr 1fr;
  -ms-grid-column-align: center;
  grid-gap: 15px;
  margin-left: -1%;
  margin-right: 1%;
  margin-top: 7%;
  margin-bottom: 3.3%;
  align-items: center;
}
.grid-display Button {
  font-family: Montserrat-Bold;
  opacity: 0.6;
}

@media (max-width: 768px) {
  .grid-display {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
