.robo-image-mobile {
  display: none;
}
@media screen and (max-width: 1500px) {
  .robo-image {
    max-width: 30% !important;
  }
}
@media screen and (max-width: 1280px) {
  .section-half:nth-child(1) {
    height: 70vh;
  }
  .section-half {
    justify-content: space-around !important;
    flex-direction: row;
    align-items: center;
  }
  .robo-image {
    display: none;
  }
  .robo-image-mobile {
    display: block;
    max-width: 35%;
    animation: float-mobile infinite ease-in-out 1.2s alternate-reverse;
  }
  .section-half:nth-child(1) .details h1 {
    font-size: 3.8rem;
  }
  .section-half:nth-child(1) .details p {
    font-size: 1rem;
  }
  .course-list {
    height: 30vh !important;
  }
  .course-container {
    flex-direction: row;
    align-items: center;
    justify-content: space-between !important;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-self: center;
    position: relative;
  }
  .course-list h3 {
    align-self: flex-start;
    font-size: 1.1rem;
    position: relative;
  }
  .course-container h3 {
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
    position: absolute;
    left: 0;
    top: 0;
  }
  .course-row1,
  .course-row2 {
    width: 50%;
    justify-content: space-around;
    margin-top: 30px;
  }
  .course-row2 {
    margin-left: 30px;
  }
}
@media screen and (max-width: 1100px) {
  .section-half:nth-child(1) .details h1 {
    font-size: 3rem;
  }
}

@-webkit-keyframes float {
  from {
    top: 48%;
    transform: translate3d(-54%, -48%, 0);
  }
  to {
    top: 52%;
    transform: translate3d(-54%, -52%, 0);
  }
}

@keyframes float {
  from {
    top: 48%;
    transform: translate3d(-54%, -48%, 0);
  }
  to {
    top: 52%;
    transform: translate3d(-54%, -52%, 0);
  }
}

@-webkit-keyframes float-mobile {
  from {
    margin-top: -10px;
  }
  to {
    margin-top: 10px;
  }
}

@keyframes float-mobile {
  from {
    margin-top: -10px;
  }
  to {
    margin-top: 10px;
  }
}

