h4,
h5,
h6 {
  font-family: Montserrat-Bold !important;
}
p,
small,
li {
  font-family: Montserrat-Regular !important;
  letter-spacing: 0.8px !important;
  line-height: 28px !important;
}
.below-title-button {
  vertical-align: middle;
  /* z-index: 3; */
  justify-content: center;
  background: #e6fefc !important;
  color: #06bfad !important;
  font-family: Montserrat-Regular !important;
  box-shadow: 2px 2px 10px 0 #e6fefc;
  text-transform: uppercase !important;
  font-size: 9px !important;
  font-weight: bold;
}
.cost {
  font-family: Montserrat-Bold;
  font-size: 14px !important;
  color: #292b2c !important;
  font-weight: bold !important;
}
.cost del {
  font-size: 10px !important;
  padding-top: 5px;
}

.videobg {
  position: relative;
  height: 300px;
  width: 100%;
  border-radius: 0 0 10px 10px !important;
  background-color: #06bfad !important;
}
.video {
  position: absolute;
  top: 12%;
  left: 7%;
  height: 300px;
  right: 8%;
}
.video img {
  width: 100%;
  min-height: 290px;
}
.videocont {
  padding-right: 0 !important;
}
.MuiBadge-anchorOriginBottomRightCircle img {
  min-height: 345px;
  float: right;
}
.triangle {
  position: absolute;
  float: right;
  top: 7%;
  animation: mymove 2000ms infinite alternate ease-in-out;
}
@keyframes mymove {
  from {
    right: 7%;
  }
  to {
    right: 5%;
  }
}
.wavelines {
  position: absolute;
  float: right;

  bottom: -2%;
  animation: wavemove 2000ms infinite alternate ease-in-out;
  animation: wavrotateemove 2500ms infinite alternate ease-in-out;
}
@keyframes wavemove {
  from {
    left: -2%;
  }
  to {
    left: -0.5%;
  }
}
@keyframes wavrotateemove {
  from {
    transform: scaleY(-0.5);
  }
  to {
    transform: scaleY(-0.7);
  }
}
.plane {
  position: absolute;
  right: -45%;
  top: 0;
  height: 700px;
  width: 350px;
}
@media (max-width: 810px) {
  .plane {
    display: none;
  }
}
@media (max-width: 767px) {
  .muicircle {
    height: 284px !important;
    width: 284px !important;
  }
  .badgeimg {
    height: 165px !important;
    width: 165px !important;
  }
  .badgeimg img {
    min-height: 260px !important;
  }
  .triangle {
    position: absolute;
    animation: mymove 2000ms infinite alternate ease-in-out;
  }
  @keyframes mymove {
    from {
      right: -3%;
    }
    to {
      right: -5%;
    }
  }
  .plane {
    position: absolute;
    right: -45%;
    top: 0;
    height: 400px;
    width: 250px;
  }
}
