.courses {
  margin-top: 9%;
}
@media (max-width: 768px) {
  .courses {
    margin-top: 30%;
  }
}
.courses h4 {
  font-family: Montserrat-Bold;
}
.MuiPaper-rounded {
  border-radius: 10px;
}
.MuiListItemIcon-root {
  min-width: 35px !important;
  color: unset !important;
}
.MuiDivider-root {
  background-color: #06bfad !important;
}
.watch,
.prize {
  font-family: Montserrat-Bold;
  font-size: 15px !important;
  color: #06bfad !important;
  font-weight: bold !important;
}
.prize del {
  font-size: 10px !important;
  font-family: Montserrat-Regular;
}
.MuiCardActions-root {
  background-image: url("../../public/icons/btnwave.svg");
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 0 !important;
  margin-left: 0 !important;
  margin-right: 0 !important;
  border-top: none !important;
}
.MuiCardActions-root:hover Button {
  border: none !important;
  outline: none !important;
}
.watch {
  font-family: Montserrat-Bold;
  font-size: 15px !important;
  color: #fff !important;
  font-weight: bold !important;
}
.a {
  position: relative;
  top: 2px;
  right: 1%;
  text-align: center;
  color: white;
  float: right;
  height: 80px;
  width: 70px;
}
.tag {
  position: absolute;
  top: -7px;
  right: 2%;
  z-index: 0;
  float: right;
  overflow: hidden;
}
.discount {
  color: white;
  text-align: center;
  position: relative;
  padding-right: 6px;
  padding-left: 6px;
  font-size: 20px;
  font-family: Montserrat-Regular;
  line-break: anywhere;
}
