.grid-display {
  display: grid;
  grid-template-columns: 1fr 1fr 0.9fr 0.6fr 1.5fr 1fr;
  -ms-grid-column-align: center;
  grid-gap: 15px;
  margin-left: -1%;
  margin-right: 1%;
  margin-top: 7%;
  margin-bottom: 3.3%;
  align-items: center;
}
.grid-display Button {
  font-family: Montserrat-Bold;
  opacity: 0.6;
}

@media (max-width: 768px) {
  .grid-display {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
  .poster {
    padding-right: 0 !important;
    padding-left: 0 !important;
  }
}
.shadow-box-example {
  padding-left: 2%;
  padding-top: 4%;
  margin-bottom: 4%;
  height: auto;
  /* z-index: 4; */
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2) !important;
  border-radius: 10px !important;
  background: url("../../public/icons/wave.svg") !important;
  background-repeat: no-repeat !important;
  background-size: cover !important;
}
.shadow-box-example hr {
  width: 20%;
  height: 0.9px;
  opacity: 0.5;
  float: left;
  margin-top: 0;
  margin-bottom: 4%;
  position: absolute;
  background: #737373;
}
.btn-group {
  margin-top: 3%;
  /* z-index: 5; */
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2);
}
.btn-group button {
  font-size: 12px;
  font-weight: 500;
}
.shadow-box-example h2 {
  margin-bottom: 0;
}
.MuiBox-root {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
@media (max-width: 800px) {
  .shadow-box-example {
    margin: 1%;
    padding: 0;
    background: none !important;
    justify-content: left !important;
  }
  .mb-5 {
    padding: 1em;
    margin-top: 5%;
    margin-bottom: 5% !important;
  }
  .shadow-box-example h2 {
    font-size: 20px;
  }
  .shadow-box-example .robo {
    display: none;
  }

  .Mui-selected {
    box-shadow: 0 1px 8px 0 #06bfad !important;
  }

  .MuiBox-root-9 {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}
.shadow-box-example .pink-dot {
  list-style: none;
  margin-top: 4%;
}

.shadow-box-example .pink-dot li {
  font-family: Montserrat-Regular;
  font-size: 15px;
  letter-spacing: 0.7px;
  line-height: 40px !important;
  display: flex;
}
.shadow-box-example .pink-dot li::before {
  content: "•";
  color: #e03268;
  width: 25px;
  display: inline-block;
  line-height: 30px;
  margin-left: -0.7em;
  font-size: 55px;
}
.MuiBox-root-6 {
  padding-left: 0px !important;
  padding-right: 0px !important;
  padding-bottom: 0px !important;
}
.MuiAppBar-colorDefault {
  background: none !important;
}
.MuiAppBar-root {
  /* z-index: 1 !important; */
}
.MuiSvgIcon-root {
  background: #e6e6e6;
  border-radius: 20px;
  padding: 2px;
}
.MuiTab-wrapper {
  flex-direction: row !important;
  text-transform: capitalize;
  font-family: Montserrat-Bold;
  font-size: 15px;
  font-weight: bold;
  /* z-index: 5; */
}

.MuiTab-wrapper svg {
  margin-right: 5px;
}

.PrivateTabIndicator-root-2 {
  display: none !important;
}
.MuiTab-labelIcon {
  min-height: 20px !important;
}
.Mui-selected {
  border-radius: 25px !important;
  /* z-index: 4; */
  justify-content: center;
  background: #06bfad !important;
  border: none !important;
  outline: none !important;
  color: #fff !important;
  font-family: Montserrat-Bold;
  box-shadow: 2px 2px 10px 0 #06bfad;
}
.Mui-selected .MuiTab-wrapper {
  color: white;
}
